.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.form-switch .form-check-input{
  margin-left: -0.5em !important;
}
/* This is navbar-red */
.navbar-red a, .navbar-red a:hover, .navbar-red button .navbar-red ul li{
  color: #FFDEAD;
}
.navbar-red button:hover{
  background-color: #FFDEAD;
  color: #D21F3C;
}
.text-modeColor, .navbar-red .dropdown-menu li{
  color: #FFDEAD;
  border-bottom: #FFDEAD;
}
.dropdown-menu li{
  padding: 5px 10px;
}
.bg-red, .navbar-red .dropdown-menu{
  background-color: #D21F3C;
}
.navbar-red .dropdown-menu li:hover{
  background-color: none;
  color: none;
}
/* This is navbar-dark */
.bg-dark, .navbar-dark .dropdown-menu{
  background-color: #212921;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
